body {
  margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display:flex;
  justify-content: center;
  align-items: center;
  padding:auto;
}

.container{
  margin-top: 30vh;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.colorbox{
  max-width:200px;
  max-height:200px;
  width:100vw;
  height:100vw;
  border:solid 3px black;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5%;
}
.colorText{
  font-family: "Exo 2";
  font-weight: bold;
  font-size: 100%;
}
.searchbox{
  margin:5%;
}
.color-selector{
  max-width:200px;
  width:100vw;
  height:30px;
  font-size: 14px;
  border-radius: 5%;
}